.Question__question,
.Question__answers {
  font-size: 32px;
  line-height: 1.5em;
  color: #000;
  text-align: left;
}

.Question__text {
  font-weight: bold;
}

.Question__answers {
  border-radius: 8px;
  background-color: #f0f0f0;
}
.Question__answers-list {
  list-style: none;
}

.Question__answer {
  cursor: pointer;
  border-radius: 12px;
  background-color: #FFF;
  border: solid 2px #CCC;
}



