.AppHeader {
  text-align: center;
  height: 48px;
}

.AppHeader_mode_LOGO {
  background-image: url('./oklogo.svg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: auto 48px;
}

.AppHeader_mode_SCORES {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.AppHeader__question-progress {
  width: 128px;
  border-radius: 23.5px;
  background-color: #e0e0e0;
  text-align: center;

  color: #FFF;
}

.AppHeader__question-progress,
.AppHeader__score {
  height: 48px;
  line-height: 48px;
  font-size: 24px;
  font-weight: bold;
}

.AppHeader__score {
  display: inline-block;
  text-align: right;
}
.AppHeader__score + .AppHeader__score {
  margin-left: 30px;
}
.AppHeader__score-label {
  color: #999;
}
.AppHeader__score-value {
  color: #333;
}
.AppHeader__score-diff {
  color: #999;
}
.AppHeader__score-diff_positive {
  color: #0fd331;
}
.AppHeader__score-diff_negative {
  color: #ff3333;
}



