.Results {
  padding-top: 0;
}

.Results__intro {
  padding: 74px 80px;
  border-radius: 4px;
  background-color: #000;

  color: #FFF;
  font-size: 48px;
  line-height: 64px;
}

.Results__table {
  width: 100%;
  text-align: left;
  line-height: 2.5;
  font-weight: bold;
  font-size: 32px;
  color: #333;
}
.Results__table-head-title {
  font-size: 48px;
}
.Results__table-head-scores {
  text-align: right;
}
.Results__table-head-delimiter {
  height: 2px;
  background-color: #cccccc;
  margin: 0 40px;
}
.Results__table-result-index {
  color: #ee8208;
}
.Results__table-result-name {
}
.Results__table-result-score {
  color: #999;
  text-align: right;
}



