.Button {
  display: inline-block;
  min-width: 300px;
  padding: 0 20px;
  height: 80px;
  line-height: 80px;
  border-radius: 40px;
  -webkit-box-shadow: 0 8px 24px 0 rgba(237, 178, 20, 0.5);
          box-shadow: 0 8px 24px 0 rgba(237, 178, 20, 0.5);
  background-color: #f08200;

  font-size: 32px;
  font-weight: bold;
  text-align: center;
  color: #FFF;

  cursor: pointer;
}

.Button_disabled {
  background-color: #999;
  -webkit-box-shadow: 0 8px 24px 0 rgba(153, 153, 153, 0.5);
          box-shadow: 0 8px 24px 0 rgba(153, 153, 153, 0.5);
  cursor: default;
}



