.App {
  text-align: center;
  width: 100%;
  padding: 80px 0;

  background-image: -webkit-linear-gradient(331deg, #f7d627, #ecac11);

  background-image: -o-linear-gradient(331deg, #f7d627, #ecac11);

  background-image: linear-gradient(119deg, #f7d627, #ecac11);

  font-family: Arial;
}

.App__container {
  width: 1200px;
  margin: 0 auto;
  border-radius: 8px;
  background-color: #ffffff;
  -webkit-box-shadow: 0 8px 48px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 8px 48px 0 rgba(0, 0, 0, 0.25);
}



