.Registration__input {
  outline: none;
}

.Registration__input_invalid {
  -webkit-box-shadow: inset 0 -4px 0 0 red;
          box-shadow: inset 0 -4px 0 0 red;
}

.Registration__input {
  display: inline-block;
  width: 100%;
  font-size: 32px;
  color: #333;
  border: none;
  -webkit-box-shadow: inset 0 -4px 0 0 #e0e0e0;
          box-shadow: inset 0 -4px 0 0 #e0e0e0;
  padding: 22px 0;
}
.Registration__input::-webkit-input-placeholder {
  color: #999;
}
.Registration__input:-ms-input-placeholder {
  color: #999;
}
.Registration__input::-ms-input-placeholder {
  color: #999;
}
.Registration__input::placeholder {
  color: #999;
}



